<template>
  <div class="container">
    <mt-header fixed title="帮助与反馈">
        <router-link to="/help" slot="left">
            <mt-button icon="back"></mt-button>
        </router-link>
    </mt-header>
    <section class="banner_middle" style="margin-top: 35px;">
      请描述你要反馈的问题
    </section>
    <section class="list">
      <label><span>登录失败</span><span><input type="radio" name="reason" v-model="dataModel" value="loginFail"></span></label>
      <label><span>直播闪退</span><span><input type="radio" name="reason" v-model="dataModel" value="exitLiveRoom"></span></label>
      <label><span>直播卡帧</span><span><input type="radio" name="reason" v-model="dataModel" value="liveCaton"></span></label>
      <label><span>积分未到账</span><span><input type="radio" name="reason" v-model="dataModel" value="notGetIntegral"></span></label>
      <label><span>奖品未收到</span><span><input type="radio" name="reason" v-model="dataModel" value="notGetGift"></span></label>
      <label><span>其他</span><span><input type="radio" checked name="reason" v-model="dataModel" value="other"></span></label>
    </section>
    <section class="banner_middle">
      问题和建议
    </section>
    <section>
      <textarea name="contemt" v-model="content" id="content" rows="7" maxlength="150" placeholder="请您填写在使用过程中的问题还有您对我们的建议，以便我们提供更好的服务"></textarea>
      <div class="submit"><button @click="btnSbmFeedback">确认提交</button></div>
    </section>
  </div>
</template>
<script>
export default {
  data() {
    return {
      centerTitle: "帮助与反馈",
      content: "",
      dataModel: "",
      topicTitle: "反馈成功",
      showTopic: 0
    };
  },
  methods: {
    back() {
      this.$router.push({
        path: "/help"
      });
    },
    btnSbmFeedback() {
      this.axios
        .post("/api/user_feedback", {
          title: this.dataModel,
          content: this.content
        })
        .then(data => {
          if (data.data == "反馈成功!") {
            this.$Toast({
              message: '反馈成功',
              position: 'middle',
              duration: 1000
            })
            setTimeout(() => {
              this.$router.push("/mine")
            }, 1000)
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>
<style lang="less" scoped>
.banner_middle {
  padding: 20px 30px;
  font-size: 35px;
  background-color: #eee;
  color: #888;
}
.list {
  label {
    display: block;
    width: 100%;
    display: flex;
    background-color: #fff;
    span {
      display: inline-block;
      height: 90px;
      line-height: 90px;
    }
    & > span:nth-child(1) {
      flex: 5;
      padding-left: 30px;
    }
    & > span:nth-child(2) {
      padding-right: 30px;
    }
  }
}
#content {
  resize: none;
  width: 100%;
  border: 0;
  padding: 0;
  outline: 0;
  padding: 30px;
  box-sizing: border-box;
  font-size: 30px;
  color: #666;
  &::-webkit-input-placeholder {
    color: #ccc;
    font-size: 30px;
  }
}
.submit {
  width: 100%;
  text-align: center;
  & > button {
    margin-top: 30px;
    width: 80%;
    height: 70px;
    border: none;
    outline: none;
    color: #44b336;
    background-color: #fff;
    font-size: 35px;
  }
}
</style>



